<template>
  <div id="login_cont">
    <div class="form_cont">
      <form action="#" @submit.prevent="register_account()">
        <h2>Register</h2>
        <div class="err" v-if="err != ''">{{ err }}</div>
        <div class="msg" v-if="msg != ''">{{ msg }}</div>
        <div class="frm_group">
          <label for="">Email</label>
          <input v-model="email" type="email" required />
        </div>
        <div class="frm_group">
          <label for="">Password</label>
          <input v-model="password" type="password" required />
        </div>
        <div class="frm_group">
          <label for="">Confirm Password</label>
          <input v-model="conpassword" type="password" required />
        </div>
        <button type="submit" class="login_btn">Register</button>
        <div class="login_frm_ftr">
          <span
            >Already have an account?
            <router-link to="login">Login Here</router-link></span
          >
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "sub_register",
  data() {
    return {
      email: "",
      password: "",
      conpassword: "",
      err: "",
      msg: "",
    };
  },
  methods: {
    register_account() {
      if (this.password != this.conpassword) {
        this.err = "Password are not matched!";
        return;
      }

      let self = this;
      let fdata = {
        username: this.email,
        password: this.password,
      };

      this.$store
        .dispatch("users/register_account", fdata)
        .then(() => {
          self.msg = "Registered Successfully!";
          self.err = "";
          self.email = "";
          self.password = "";
          self.conpassword = "";
        })
        .catch((err) => {
          self.err = err.status;
        });
    },
  },
};
</script>

<style scoped>
.err {
  background: #ee6b6b;
  text-align: center;
  margin: 0 0 20px;
  padding: 13px;
  border-radius: 4px;
  color: #fff;
}
.msg {
  background: #55c577;
  text-align: center;
  margin: 0 0 20px;
  padding: 13px;
  border-radius: 4px;
  color: #fff;
}
#login_cont {
  width: 442px;
  margin: 184px auto 0;
}
#login_cont h2 {
  text-transform: uppercase;
  font-size: 30px;
  line-height: 100%;
  margin: 0 0 49px;
  font-weight: bold;
}

.form_cont {
}
.form_cont form {
  font-weight: 300;
  text-transform: none;
}
.frm_group {
  margin: 0 0 18px;
}
.frm_group label {
  margin: 0 0 5px;
  display: block;
}
.frm_group input {
  display: block;
  width: 100%;
  border: 1px solid #111;
  font-size: 20px;
  padding: 17px 10px;
  font-weight: normal;
}
.login_btn {
  width: 100%;
  background: #111;
  color: #fff;
  font-family: inherit;
  border: none;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: bold;
  padding: 23px 10px;
}
.login_frm_ftr {
  text-align: center;
  font-size: 17px;
  margin: 36px 0 0;
}
.login_frm_ftr a {
  font-weight: bold;
  color: #111;
  text-transform: uppercase;
}
</style>

